import React, {useEffect, useState } from 'react'
import { Form, Button, Row, Card, Col } from 'react-bootstrap'
import data from "../assets/data.json"
import { picarr } from '../assets/images/piclist'

const PaperBrowser = () =>{

    const[Selectedlist, Setselectedlist] = useState({
        Title: 'None',
        Year: 'None',
        SixCanons: 'None',
        Author: 'None',
        ComTech: 'None',
        AnaFrame: 'None'
    })

    const[showArray, Setshowarray] = useState([]);

    var currentChoice = {
        Title: 'None',
        Year: 'None',
        SixCanons: 'None',
        Author: 'None',
        ComTech: 'None',
        AnaFrame: 'None'
    }

    const handlethis = (e) =>{
        console.log(e.checked);
        if (e.checked){
            console.log(e)
        }
    }

    const handleClick = (e) =>{
        Setselectedlist(currentChoice);
    }

    useEffect(() => {
        Setshowarray(data);

    },[])
    
    return(
        <div className='container'>
            <div id="h">
                <div id="head1"  style={{height: '100px'}}>
                    <span className="title">Survey on Computational Approaches in Traditional Chinese Painting</span>
                    <div className="menu">
                        <a href="/survey-webpage/home">Introduction</a>
                        <a href="/survey-webpage/overview">Overview</a>
                        <a id="selected" href="/survey-webpage/paper-browser">Paper Browser</a>
                    </div>
                </div>
            </div>

            <Row xs={2} md={2} lg={2} xxl={2} className="g-4">
            <div className='checkbox'>
                <h3>The Six Canons</h3>
                <Form.Check 
                    className='six'
                    id='six-1'
                    label='Transmission by Copying'
                    onChange={e=>handlethis(e.target)}
                />
                <Form.Check 
                    className='six'
                    id='six-2'
                    label='Division and Planning'
                    onChange={e=>handlethis(e.target)}
                />
                <Form.Check 
                    className='six'
                    id='six-3'
                    label='Suitablility to type'
                    onChange={e=>handlethis(e.target)}
                />
                <Form.Check 
                    className='six'
                    id='six-4'
                    label='Correspondence to the object'
                    onChange={e=>handlethis(e.target)}
                />
                <Form.Check 
                    className='six'
                    id='six-5'
                    label='Bone Method'
                    onChange={e=>handlethis(e.target)}
                />
                <Form.Check 
                    className='six'
                    id='six-6'
                    label='Spirit Resonance'
                    onChange={e=>handlethis(e.target)}
                />
                <h3>Computational Techniques</h3>
                <Form.Check 
                    id='ct-1'
                    label='Geometric-Based'
                    onChange={e=>handlethis(e.target)}
                />
                <Form.Check 
                    id='ct-2'
                    label='Image-Based'
                    onChange={e=>handlethis(e.target)}
                />
                <Form.Check 
                    id='ct-3'
                    label='Reinforcement Learning'
                    onChange={e=>handlethis(e.target)}
                />
                <Form.Check 
                    id='ct-4'
                    label='Diffusion Model'
                    onChange={e=>handlethis(e.target)}
                />
                <Form.Check 
                    id='ct-5'
                    label='Morphology/Graphical Methods'
                    onChange={e=>handlethis(e.target)}
                />
                <Form.Check 
                    id='ct-6'
                    label='Learned Features'
                    onChange={e=>handlethis(e.target)}
                />
                <Form.Check 
                    id='ct-7'
                    label='Handcrafted Features'
                    onChange={e=>handlethis(e.target)}
                />
                <Form.Check 
                    id='ct-8'
                    label='Others'
                    onChange={e=>handlethis(e.target)}
                />
                <Form.Check 
                    id='ct-9'
                    label='Virtual Reality'
                    onChange={e=>handlethis(e.target)}
                />
                <Form.Check 
                    id='ct-10'
                    label='Video Generation'
                    onChange={e=>handlethis(e.target)}
                />
                <Form.Check 
                    id='ct-11'
                    label='Image Generation'
                    onChange={e=>handlethis(e.target)}
                />
                <Form.Check 
                    id='ct-12'
                    label='Object Detection'
                    onChange={e=>handlethis(e.target)}
                />
                <Form.Check 
                    id='ct-13'
                    label='Image Segmentation'
                    onChange={e=>handlethis(e.target)}
                />
                <Form.Check 
                    id='ct-14'
                    label='Image Classification'
                    onChange={e=>handlethis(e.target)}
                />
                <h3>Analytical Framework</h3>
                <p style={{fontWeight:30, fontSize:15}}>Digitalization</p>
                <Form.Check 
                    id='af-1'
                    label='Retrieval'
                    onChange={e=>handlethis(e.target)}
                />
                <Form.Check 
                    id='af-2'
                    label='Restoration'
                    onChange={e=>handlethis(e.target)}
                />
                <p style={{fontWeight:30, fontSize:15}}>Interpretation</p>
                <Form.Check 
                    id='af-3'
                    label='Local Feature'
                    onChange={e=>handlethis(e.target)}
                />
                <Form.Check 
                    id='af-4'
                    label='Global Feature'
                    onChange={e=>handlethis(e.target)}
                />
                <p style={{fontWeight:30, fontSize:15}}>Creation</p>
                <Form.Check 
                    id='af-5'
                    label='Image Generation'
                    onChange={e=>handlethis(e.target)}
                />
                <Form.Check 
                    id='af-6'
                    label='Video Generation'
                    onChange={e=>handlethis(e.target)}
                />
                <p style={{fontWeight:30, fontSize:15}}>Exhibition</p>
                <Form.Check 
                    id='ct-7'
                    label='XR'
                    onChange={e=>handlethis(e.target)}
                />
                <Form.Check 
                    id='ct-8'
                    label='Interact'
                    onChange={e=>handlethis(e.target)}
                />
                <Form.Check 
                    id='ct-9'
                    label='Others'
                    onChange={e=>handlethis(e.target)}
                />
                <br />
                <Button variant="primary" onClick={handleClick}></Button>
            </div>

            <div className='paper-cards'>
            <Row xs={1} md={3} className="g-4">
                {showArray.map(function(p,index) { 
                    const ind = Number(p.picture)                                     
                     return(
                    <Col>
                    <Card className='mt-3'  style={{ width: '15rem' }}>
                    <Card.Img variant="top" src={picarr[ind-1]} />
                    <Card.Body>
                        <Card.Title>{p.Title}</Card.Title>     
                        <Button variant="primary" href={p.Link}></Button>                   
                    </Card.Body>
                    </Card>
                </Col>
                )})}
            </Row>
            </div>
            </Row>
        </div>            
    )
}

export default PaperBrowser;