import React,{useEffect}from 'react';
import './home.css';
import {csvToJson} from 'csvtojson';
const csvFilePath = './assets/data.csv';


const Home = () =>{

    return(
    <div className='container'>
        <div id="h">
            <div id="head1"  style={{height: '73px'}}>
                <span className="title">Survey on Computational Approaches in Traditional Chinese Painting</span>
                <div className="menu">
                    <a id="selected" href="/survey-webpage/home">Introduction</a>
                    <a href="/survey-webpage/overview">Overview</a>
                    <a href="/survey-webpage/paper-browser">Paper Browser</a>
                </div>
            </div>
        </div>  

        <div id = 'intro-abstract'>
        Traditional Chinese Painting (TCP) is an invaluable cultural heritage resource and a unique visual art style. In recent years, increasing interest has been placed on digitalizing TCPs to preserve and revive the culture. The resulting digital copies have enabled the advancement of computational methods for structured and systematic understanding of TCPs. To explore this topic, we conducted an in-depth analysis of 92 pieces of literature. We examined the current use of computer technologies on TCPs from three perspectives, based on numerous conversations with specialists. First, in light of the ``Six Principles of Painting" theory, we categorized the articles according to their research focus on artistic elements. Second, we created a four-stage framework to illustrate the purposes of TCP applications. Third, we summarized the popular computational techniques applied to TCPs. The framework also provides insights into potential applications and future prospects, with professional opinion. 
        </div>
    </div>
    )
}

export default Home;